            :value="item.value"
          ></el-option>
        </el-select>
      </div>

      <el-container>
        <el-table :data="tableData" style="width: 100%" :row-class-name="rowIndex">
          <el-table-column :formatter="order" label="" width="180"></el-table-column>
          <el-table-column prop="name" label=""></el-table-column>
          <el-table-column prop="classification" label=""></el-table-column>
          <el-table-column prop="type" label=""></el-table-column>
          <el-table-column prop="bookNo" label=""></el-table-column>
          <el-table-column prop="date" label=""></el-table-column>
          <el-table-column prop="limitedPeriod" label=""></el-table-column>
          <el-table-column prop="yield" label=""></el-table-column>
          <el-table-column prop="yield" label=""></el-table-column>
          <el-table-column label="" class-name="text-center">
            <el-tooltip class="item" effect="dark" content="" placement="top">
              <el-button size="small" icon="el-icon-search" circle plain type="success"></el-button>
            </el-tooltip>
            <el-tooltip class="item" effect="dark" content="" placement="top">
              <el-button
                size="small"
                icon="el-icon-goods"
                circle
                @click="goBussinessProducts"
                plain
                type="primary"
              ></el-button>
            </el-tooltip>
          </el-table-column>
        </el-table>
      </el-container>
      <div class="pageBox">
        <el-pagination
          v-if="total > page.pageSize"
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
          :current-page="page.pageIndex"
          :page-sizes="[10, 20, 50]"
          :page-size="page.pageSize"
          layout="total, sizes, prev, pager, next, jumper"
          :total="total"
        ></el-pagination>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      page: {
        pageIndex: 1,
        pageSize: 10
      },
      total: 100,
      options: [
        {
          value: "",
          label: ""
        }
      ],
      tableData: [
        {
          name: "",
          classification: "",
          type: "",
          bookNo: "123456",
          date: "2018-09-012020-05-01",
          limitedPeriod: "1",
          yield: "600"
        },
        {
          name: "",
          classification: "",
          type: "",
          bookNo: "123456",
          date: "2018-09-012020-05-01",
          limitedPeriod: "1",
          yield: "600"
        },
        {
          name: "",
          classification: "",
          type: "",
          bookNo: "123456",
          date: "2018-09-012020-05-01",
          limitedPeriod: "1",
          yield: "600"
        },
        {
          name: "",
          classification: "",
          type: "",
          bookNo: "123456",
          date: "2018-09-012020-05-01",
          limitedPeriod: "1",
          yield: "600"
        }
      ]
    };
  },
  methods: {
    rowIndex({ row, rowIndex }) {
      row.rowIndex = rowIndex;
    },
    order(row) {
      return this.page.pageSize * (this.page.pageIndex - 1) + row.rowIndex + 1;
    },
    //
    handleSizeChange(val) {
      let that = this;
      that.page.pageSize = val;
      that.fullPage(1);
    },
    //
    handleCurrentChange(val) {
      let that = this;
      that.page.pageIndex = val;
      that.fullPage(1);
    },
    //
    goBussinessProducts() {
      this.$router.push({ path: "businessProducts" });
    }
  }
};
</script>

